import React, { useEffect, useState } from "react";
import serviceproduct from "../../service/product";
import { Row, Col, Image } from "antd";
import Carasoul from "../../components/Carasoul";

export default function HomePage({ content }) {

    return <>
        <Row justify1="space-between" style={{ marginTop: 20 }}>
            <Col
                xs={{ span: 24 }}
                sm={{ span: 24 }}
                md={{ span: 5 }}
                lg={{ span: 5 }}
            >
                <Row justify="space-around">
                    <Col>
                        <Image
                            preview={false}
                            src="https://images.unsplash.com/photo-1595500382155-e7cd8180c301"
                            alt=""
                            style={{ width: "240px", height: "240px" }}
                        />
                    </Col>
                </Row>
            </Col>
            <Col
                xs={{ span: 24 }}
                sm={{ span: 24 }}
                md={{ span: 14 }}
                lg={{ span: 14 }}
            >
                <Carasoul
                    content={content}
                    contentStyle={{
                        objectFit: "cover",
                        height: "240px",
                        width: "240px",
                        background: 'white'
                    }}
                    slidesToShow={3}
                    className="home-page-product-carasoul"
                    responsive={[
                        {
                            breakpoint: 768,
                            settings: {
                                slidesToShow: 2,
                                arrows: false
                            }
                        },
                        {
                            breakpoint: 576,
                            settings: {
                                slidesToShow: 1,
                                arrows: false
                            }
                        }
                    ]}
                />
            </Col>
            <Col
                xs={{ span: 24 }}
                sm={{ span: 24 }}
                md={{ span: 5 }}
                lg={{ span: 5 }}
            >
                <Row justify="space-around">
                    <Col>
                        <Image
                            preview={false}
                            src="https://images.unsplash.com/photo-1595500382155-e7cd8180c301"
                            alt=""
                            style={{ width: "240px", height: "240px" }}
                        />
                    </Col>
                </Row>
            </Col>
        </Row>
    </>
};