import { Link } from "react-router-dom";
import {
    InstagramOutlined,
    FacebookOutlined,
    LinkedinOutlined,
    TwitterOutlined,
} from "@ant-design/icons";

export default function Footer() {
    return (
        <>
            <footer className='footer-wrapper'>
                <div className="footer-content-wrapper">
                    <h2 className="content-title">Support</h2>
                    <Link to={"#"} className="footer-link">Customer Support</Link>
                    <Link to={"#"} className="footer-link">FAQ</Link>
                    <Link to={"#"} className="footer-link">Webinars</Link>
                    <Link to={"#"} className="footer-link">Contact us</Link>
                </div>
                <div className="footer-content-wrapper">
                    <h2 className="content-title">Company</h2>
                    <Link to={"#"} className="footer-link">About us</Link>
                    <Link to={"#"} className="footer-link">Events</Link>
                    <Link to={"#"} className="footer-link">Officers</Link>
                    <Link to={"#"} className="footer-link">Gallery</Link>
                    <Link to={"#"} className="footer-link">Feedback</Link>
                </div>
                <div className="footer-content-wrapper">
                    <h2 className="content-title">Social Media</h2>
                    <div className="socials">
                        <Link to={"#"} className="footer-link"><FacebookOutlined /></Link>
                        <Link to={"#"} className="footer-link"><InstagramOutlined /></Link>
                        <Link to={"#"} className="footer-link"><LinkedinOutlined /></Link>
                        <Link to={"#"} className="footer-link"><TwitterOutlined /></Link>

                    </div>
                </div>
                <div className="footer-content-wrapper office-detail-wrapper">
                    <h2 className="content-title">Registered Office Address:</h2>
                    <span className="office-details">111, 1st Floor, Best Business Park Tower
                        Netaji Subhash Palace
                        New Delhi- 110034</span>
                    <span className="office-details">(+91) 01149856360 (+91) 01145627316</span>
                    <span className="office-details">(+91) 9810885930</span>
                    <span className="office-details">info@amitlabs.com
                        sales@amitlabs.com</span>
                </div>
            </footer>
        </>
    );
}