import React from 'react'
import { Row, Col, Button, Space, Dropdown, Affix } from 'antd'
import { DownOutlined, HomeFilled, ShoppingCartOutlined, SmileOutlined } from "@ant-design/icons"
import { Link } from 'react-router-dom'

const CategoryNav = () => {
    const items = [
        {
            key: '1',
            label: (
                <a target="_blank" rel="noopener noreferrer" href="https://www.antgroup.com">
                    1st menu item
                </a>
            ),
        },
        {
            key: '2',
            label: (
                <a target="_blank" rel="noopener noreferrer" href="https://www.aliyun.com">
                    2nd menu item (disabled)
                </a>
            ),
            icon: <SmileOutlined />,
            disabled: true,
            // ch
        },
        {
            key: '3',
            label: (
                <a target="_blank" rel="noopener noreferrer" href="https://www.luohanacademy.com">
                    3rd menu item (disabled)
                </a>
            ),
            disabled: true,
        },
        {
            key: '4',
            danger: true,
            label: 'a danger item',
        },
    ];
    return (
        <Affix className='categoryNav'>
            <Row className="bg-gradient" justify="space-around" style={{overflow:"auto"}}>
                <Space size={60}>
                    <Space>
                    <Link to={"/"} style={{ color: 'white'}}> 
                        <HomeFilled style={{ fontSize: "1.8rem", color: "white" }} />
                    </Link>
                    </Space>
                    <Dropdown
                        menu={{
                            items,
                        }}
                    >
                        <a onClick={(e) => e.preventDefault()} style={{ color: "white" }}>
                            <Space style={{ fontSize: "1.8rem" }}>
                                Brands
                                <DownOutlined style={{ fontSize: ".8rem" }} />
                            </Space>
                        </a>
                    </Dropdown>
                    <Dropdown
                        menu={{
                            items,
                        }}
                    >
                        <a onClick={(e) => e.preventDefault()} style={{ color: "white" }}>
                            <Space style={{ fontSize: "1.8rem" }}>
                                Chemicals
                                <DownOutlined style={{ fontSize: ".8rem" }} />
                            </Space>
                        </a>
                    </Dropdown>
                    <Dropdown
                        menu={{
                            items,
                        }}
                    >
                        <a onClick={(e) => e.preventDefault()} style={{ color: "white" }}>
                            <Space style={{ fontSize: "1.8rem" }}>
                                Life Science
                                <DownOutlined style={{ fontSize: ".8rem" }} />
                            </Space>
                        </a>
                    </Dropdown>
                    <Dropdown
                        menu={{
                            items,
                        }}
                    >
                        <a onClick={(e) => e.preventDefault()} style={{ color: "white" }}>
                            <Space style={{ fontSize: "1.8rem" }}>
                                Gases
                                <DownOutlined style={{ fontSize: ".8rem" }} />
                            </Space>
                        </a>
                    </Dropdown>
                    <Dropdown
                        menu={{
                            items,
                        }}
                    >
                        <a onClick={(e) => e.preventDefault()} style={{ color: "white" }}>
                            <Space style={{ fontSize: "1.8rem" }}>
                                Labware
                                <DownOutlined style={{ fontSize: ".8rem" }} />
                            </Space>
                        </a>
                    </Dropdown>
                    <Dropdown
                        menu={{
                            items,
                        }}
                    >
                        <a onClick={(e) => e.preventDefault()} style={{ color: "white" }}>
                            <Space style={{ fontSize: "1.8rem" }}>
                                Lab instruments
                                <DownOutlined style={{ fontSize: ".8rem" }} />
                            </Space>
                        </a>
                    </Dropdown>
                </Space>
                <Link to={"/quickorder"} style={{ color: 'white' }}>
                    <button className="primary-color font-bold" style={{ fontSize: "1.6rem", padding: ".6rem .5rem", cursor: 'pointer' }}>Quick Order</button>
                </Link>
                {/* <CategoryMenu /> */}
            </Row>
        </Affix>
    )
}

export default CategoryNav