import React, { useEffect, useState } from "react";
import { Row, Col, Button, Space, Tooltip, Tabs, Typography } from "antd";
import Search from "antd/es/input/Search";
import {
    MenuOutlined,
    SearchOutlined,
    ShoppingCartOutlined,
    UserOutlined,
} from "@ant-design/icons";
import Logo from "../Assets/logo.png";
import { Link } from "react-router-dom";
// import Login from "../Pages/Login";
// import MyCart from "../Pages/MyCart";

export default function TopHeader () {
    const [isModalOpen, setIsModalOpen] = useState(false);

    const handleClick = () => {
        setIsModalOpen(true);
    };

    const items = [
        {
            key: "1",
            label: (
                <Typography.Title level={4} strong>
                    My Cart (4)
                </Typography.Title>
            ),
            children: (
                <>
                    {/* <MyCart></MyCart> */}
                </>
            ),
        },

        {
            key: "2",
            label: (
                <Typography.Title level={4} strong>
                    EUD CART (4)
                </Typography.Title>
            ),
            children: (
                <>
                    {/* <MyCart></MyCart> */}
                </>
            ),
        },
        {
            key: "3",
            label: (
                <Link to={"viewall"}>
                    <Typography.Title level={4} strong>
                        View All
                    </Typography.Title>
                </Link>
            ),
        },
    ];

    const tt = (
        <>
            <Tabs items={items}></Tabs>
        </>
    );

    return (
        <Row
            style={{
                display: "flex",
                alignItems: "center",
                padding: "2rem 10rem 2rem 1rem",
                overflow: "hidden",
                justifyContent: "center",
                padding: "2rem 2.5rem",
            }}
            className="nav-wrapper"

        >
            <Col
                span={10}
                style={{
                    fontSize: "1.6rem",
                    fontWeight: "600",
                    display: "flex",
                    alignItems: "center",
                }}

                className="logo-wrapper"
            >
                <img src={Logo} alt="amit-labs-logo" width="200px" className="logo" />
            </Col>

            <Col style={{ display: "flex", alignItems: "center" }}>

                <Space size={"large"}>
                    <Search
                        placeholder="Search your Product"
                        enterButton
                        style={{ width: "600px", background: "grey" }}
                        className="searchBox"
                    />
                    <div className="icon-wrapper-header">
                        <UserOutlined className="headerIcon" />
                        <SearchOutlined className="searchIcon headerIcon" />
                        <ShoppingCartOutlined className="headerIcon" />
                        <MenuOutlined className="headerIcon" />
                    </div>
                    <Button
                        onClick={() => handleClick()}
                        style={{
                            background:
                                "linear-gradient(90deg, rgba(121,79,153,1) 22%, rgba(150,79,153,1) 58%, rgba(97,79,153,1) 90%)",
                            color: "white",
                        }}
                        className="loginBtn"
                    >
                        Login
                    </Button>
                    {/* <Login
                        isModalOpen={isModalOpen}
                        handleClick={handleClick}
                        setIsModalOpen={setIsModalOpen}
                    /> */}
                    <></>

                    <Link to={"/profile"} style={{ color: "black" }}>
                        <Button
                            style={{
                                background:
                                    "linear-gradient(90deg, rgba(121,79,153,1) 22%, rgba(150,79,153,1) 58%, rgba(97,79,153,1) 90%)",
                                color: "white",
                            }}
                            className="profileBtn"
                        >
                            <UserOutlined />
                        </Button>
                    </Link>
                    <Tooltip
                        color="white"
                        autoAdjustOverflow={true}
                        align={"middle"}
                        overlayInnerStyle={{
                            width: "480px",
                            right: "90%",
                            position: "relative",
                        }}
                        placement="topRight"
                        title={tt}
                    >
                        <Link to={"/cart"} style={{ color: "black" }}>
                            <Button
                                style={{
                                    color:
                                        "linear-gradient(90deg, rgba(121,79,153,1) 22%, rgba(150,79,153,1) 58%, rgba(97,79,153,1) 90%)",
                                    background: "#EFEFEF",
                                    fontWeight: "500",
                                }}
                                className="cartBtn"
                            >
                                <ShoppingCartOutlined /> Cart
                            </Button>
                        </Link>
                    </Tooltip>
                </Space>
            </Col>
        </Row>
    );
};