import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Carousel, Image } from 'antd'
import React from 'react'


const Carasoul = ({ content, contentStyle, className = "", preview = false, slidesToShow = 1, responsive = [] }) => {
    return (
        <Carousel
            autoplay
            arrows
            nextArrow={<span />}
            prevArrow={<span />}
            slidesToShow={slidesToShow}
            className={className}
            responsive={responsive}
        >
            {content?.map((item, i) => (
                <div key={i} >
                    <Image preview={preview} src={item} alt="img" style={contentStyle} />
                </div>
            ))}
        </Carousel>
    )
}

export default Carasoul;